.header-section{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 1em;
}

.image-container{
    
}

.image{
    background-color: black;
    border-radius: 5% 50% 5% 50%;
    width: 200px;
    height: 200px;
}

.header-text-container{
    display: flex;
    flex-direction: column;
}

.header-text-container > h1 {
    font-size: 3vw;
    font-weight: 400;
}

.lastname{
    font-size: 3.6vw;
    font-weight: 800; 
    color: purple; 
}

.header-text-container > h1, h4, p {
    padding: 0;
    margin: 0;
    margin-bottom: .5em;
    display: inline-block;
}

.header-text-container > p {
    font-size: 1.3vw;
    width: 60%;
}

.header-text-container > button {
    border: solid 1px purple;
    padding: .5em;
    border-radius: 10px;
    font-weight: 600;
    transition: .5s;
    width: 11em;
}

.header-text-container > button:hover{
    background-color: rgba(128, 0, 128, 0.361);
    border: solid 1px rgba(128, 0, 128, 0.361);
}